.services {
  width: 100%;
  height: auto;
  overflow: hidden;
  background-image: url(./img/Bg.png);

  &-top {
    max-width: 100%;
    background-image: url(./img/imgtop.png);
    background-size: cover;
    width: 100%;
    height: 24.5rem;
    padding-top: 3rem;

    &--title {
      background-color: #bf1616;
      color: white;
      font-family: "Kyiv Region", sans-serif;
      font-size: 48px;
      font-weight: 500;
      line-height: 6rem;
      text-transform: uppercase;
      width: 27rem;
      text-align: center;
    }
  }

  &-center {
    display: flex;
    justify-content: space-around;
    width: 100%;
    height: 38.5rem;
    position: relative;

    .services-center-ticker-wrapper {
      overflow: hidden;
      order: 1;
      height: 100%;

      .services-center-ticker {
        animation: ticker-kf 8s linear infinite;
        height: 100%;
      }
    }

    &--container {
      display: flex;
      flex-direction: column-reverse;
      align-items: center;
      gap: 2rem;
      position: relative;
      height: 100%;
    }

    & section {
      display: grid;
      grid-template-rows: repeat(5, 1fr);
      width: 24.5rem;
      height: 32.75rem;
      padding: 2rem;
      align-items: center;
    }

    .black-bg {
      background-color: black;
      color: white;
      position: relative;
      bottom: 10rem;
      box-shadow: 6px 7px 90px 0 #0000001f;

      & a {
        background-color: #fff;
        color: black;
        font-family: "IBM Plex Mono", monospace;
        font-size: 24px;
        font-weight: 500;
        line-height: 5rem;
        text-align: center;
        &:hover {
          background-color: #dddddd;
        }
      }
    }

    .white-bg {
      background-color: #fffbf6;
      position: relative;
      bottom: 4rem;
      box-shadow: 6px 7px 90px 0 #0000001f;
      order: 2;

      & a {
        background-color: #bf1616;
        color: white;
        font-family: "IBM Plex Mono", monospace;
        font-size: 24px;
        font-weight: 500;
        line-height: 5rem;
        text-align: center;
        &:hover {
          background-color: #bf16169d;
        }
      }
    }

    &--title {
      font-family: "IBM Plex Mono", monospace;
      font-size: 24px;
      font-weight: 500;
      line-height: 27px;
      letter-spacing: 0em;
    }

    &--price {
      font-family: "IBM Plex Mono", monospace;
      font-size: 20px;
      font-weight: 400;
      line-height: 24px;
    }

    &--info,
    &--details {
      font-family: "IBM Plex Mono", monospace;
      font-size: 16px;
      font-weight: 400;
      line-height: 20px;
      margin: 0;
    }

    &--details {
      align-self: start;
    }

    &--button {
      font-family: "IBM Plex Mono", monospace;
      font-size: 24px;
      font-weight: 500;
      line-height: 29px;
      width: 100%;
      height: 5rem;
    }
  }

  &-bottom {
    max-width: 100%;
    background-image: url(./img/imgbottom.png);
    background-size: cover;

    &-content {
      display: grid;
      grid-template-columns: 1fr 0.5fr;
      justify-items: center;
      align-items: center;
      width: 100%;
      height: auto;
      overflow: hidden;
      z-index: 1;
    }
    &--text {
      margin: 0;
      font-family: "IBM Plex Mono", monospace;
      font-size: 25px;
      font-weight: 500;
      line-height: 30px;
      color: white;
      padding: 1rem;
    }

    &--button {
      font-family: "Kyiv Region", sans-serif;
      font-size: 43px;
      font-weight: 500;
      line-height: 42px;
      text-transform: uppercase;
      background-color: #bf1616;
      color: white;
      text-decoration: none;
      width: 41.5rem;
      height: 27rem;
      display: flex;
      justify-content: space-around;
      align-items: center;
      position: relative;
      padding: 3rem;
      overflow: hidden;
      margin: 0 auto;
      &:hover {
        background-color: #880b08;
      }
      & img {
        position: absolute;
        top: 10rem;
        right: 5rem;
      }
    }
  }
}

.no-scroll {
  overflow: hidden;
}

@keyframes ticker-kf {
  0% {
    transform: translate3d(0, 0, 0);
  }
  100% {
    transform: translate3d(0, -100%, 0);
  }
}

.modal-overlay {
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
}

.modal-content {
  background: white;
  border-radius: 8px;
  padding: 20px;
  width: 60rem;
  max-width: 100%;
  text-align: center;
  position: relative;
  display: flex;
  flex-direction: column;
}

.question-options {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.modal-controls {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.label-class {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  justify-items: baseline;
  grid-template-rows: 1fr;
  width: 40rem;
}
.label-class,
.question-counter {
  font-family: "IBM Plex Mono", monospace;
  font-size: 24px;
  font-weight: 500;
  line-height: 3rem;
  text-align: center;
  width: 100%;
}
.input-class {
  margin: 0;
  grid-row: 1;
  grid-column: 2;
  accent-color: #bf1631;
}

.modal-inner {
  background: white;
  border-radius: 8px;
  padding: 20px;
  width: 90%;
  max-width: 100%;
  text-align: center;
  position: relative;
  & h3 {
    font-family: "Kyiv Region", sans-serif;
    font-size: 48px;
    font-weight: 500;

    text-transform: uppercase;
    width: 100%;
    text-align: center;
  }
}

.progress-bar {
  width: 100%;
  height: 10px;
  background: #ddd;
  margin: 15px 0;
  border-radius: 8px;
  overflow: hidden;
}

.progress-bar-fill {
  height: 100%;
  background: #bf1616;
}

.close-modal-button {
  position: absolute;
  top: 1.5rem;
  right: 1.5rem;
  cursor: pointer;
  background: none;
  border: none;
  font-size: 24px;
  padding: 0;
  color: #bf1616;
  width: 1rem;
  height: 1rem;
}

input[type="text"],
input[type="email"] {
  height: 35px;
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

button {
  background-color: #bf1616;
  color: white;
  border: none;
  border-radius: 8px;
  padding: 10px 20px;
  cursor: pointer;
  &:nth-child(1) {
    order: 2;
  }
}

button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.close-modal-button::before,
.close-modal-button::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 20px;
  height: 2px;
  background-color: #000;
  transition: transform 0.2s ease-in-out, background-color 0.2s ease-in-out;
}

.close-modal-button::before {
  transform: translate(-50%, -50%) rotate(45deg);
}

.close-modal-button::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}

.close-modal-button:hover::before,
.close-modal-button:hover::after {
  background-color: red;
  color: #ffffff;
}

.flag-dropdown {
  background-color: transparent !important;
  border: 0 !important;
}

.react-tel-input .selected-flag:hover,
.react-tel-input .selected-flag:focus {
  background-color: transparent !important;
}

.react-tel-input .form-control:focus {
  outline: none !important;
  box-shadow: none !important;
}

.react-tel-input .form-control {
  width: 100%;
}

@media (max-width: 1023px) {
  .modal-inner {
    width: 90%;
    padding: 1rem 1.5rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 90%;
    & h3 {
      margin-top: 3rem;
      font-size: 30px;
      line-height: 30px;
    }
  }
  .progress-bar {
    order: 3;
  }
  .question-counter {
    top: 1.4rem;
    left: 1.5rem;
    width: auto;
    position: absolute;
    text-align: start;
    color: #bf1616;
  }
  input[type="text"],
  input[type="email"] {
    width: 100%;
    padding: 5px;
    border: 1px solid #ccc;
    border-radius: 4px;
    margin-bottom: 0;
    height: 2.5rem;
  }
  .react-tel-input .form-control {
    font-size: 14px;
    border: 1px solid #cacaca;
    height: 2.5rem;
  }
  .react-tel-input .flag {
    width: 16px;
    height: 11px;
  }
  .question-counter,
  .label-class {
    font-size: 20px;
    line-height: 24px;
  }
  .label-class {
    height: 1.5rem;
    width: 100%;
    grid-template-columns: 1fr 0.1fr;
    margin-bottom: 0.25rem;
  }
  .modal-controls {
    order: 3;
    margin-top: 10px;
  }
  .input-class {
    width: 1.5rem;
    height: 1.5rem;
    justify-self: end;
  }
  .services {
    &-top {
      height: auto;
      background-image: url(./img/Bg.png);
      &--logos {
        padding-bottom: 2rem;
      }
      &--title {
        font-size: 64px;
        line-height: 61px;
        text-align: left;
        background-color: rgba(0, 0, 0, 0);
        color: black;
        width: auto;
        margin-top: 0.5rem;
      }
    }

    &-center {
      display: grid;
      overflow: hidden;
      height: auto;
      padding-bottom: 1rem;

      &--container {
        display: none;
      }

      .service-block {
        justify-items: center;
        height: auto;
        @media screen and (max-width: 1023px) {
          transform: none !important;
        }
      }

      .black-bg {
        background-color: black;
        color: white;
        position: relative;
        box-shadow: 6px 7px 90px 0 #0000001f;
        bottom: 0;

        & a {
          background-color: #fff;
          color: black;
          font-family: "IBM Plex Mono", monospace;
          font-size: 24px;
          font-weight: 500;
          line-height: 5rem;
          text-align: center;
        }
      }

      .white-bg {
        background-color: #fffbf6;
        position: relative;
        box-shadow: 6px 7px 90px 0 #0000001f;
        order: 2;
        bottom: 0;

        & a {
          background-color: #bf1616;
          color: white;
          font-family: "IBM Plex Mono", monospace;
          font-size: 24px;
          font-weight: 500;
          line-height: 5rem;
          text-align: center;
        }
      }

      section {
        width: 90vw;
        padding: 2rem;
        align-items: flex-start;
        margin-bottom: 2rem;
        text-align: left;
        background-color: #fffbf6;

        &:last-child {
          margin-bottom: 0;
        }

        &--button {
          align-self: flex-end;
        }
      }

      &--title {
        font-size: 32px;
        line-height: 30px;
        text-align: center;
      }

      &--price {
        align-self: center;
        font-size: 20px;
        line-height: 24px;
      }

      &--info,
      &--details {
        font-size: 24px;
        line-height: 29px;
        margin: 0;
        text-align: center;
      }

      &--details {
        align-self: start;
      }

      &--button {
        width: 50vw;
        height: 5rem;
        font-size: 24px;
        line-height: 29px;
      }
    }

    &-bottom {
      grid-template-columns: 1fr;
      grid-template-rows: 0.7fr 0.2fr;
      height: auto;
      padding-top: 2rem;
      &--text {
        font-size: 20px;
        line-height: 24px;
        text-align: center;
        padding-bottom: 2rem;
        & span {
          padding-top: 1rem;
          display: block;
        }
      }

      &--button {
        font-size: 30px;
        width: 100vw;
        text-align: center;
        padding: 1rem;
        height: 10rem;
        align-self: end;

        & img {
          position: relative;
          top: 0;
          right: 0;
          width: 4rem;
        }
      }
    }
  }
  .services-top--logos {
    display: flex;
    align-items: flex-start;
  }
}
