@media (max-width: 480px) {
  .modal-inner {
    width: 100%;
    height: 100%;
    & h3 {
      margin-top: 2rem;
      font-size: 18px;
      line-height: 21px;
    }
  }
  .question-counter {
    top: 1.4rem;
    left: 1.5rem;
    width: auto;
    position: absolute;
    text-align: start;
    color: #bf1616;
  }
  .close-modal-button {
    width: 0.5rem;
    height: 0.5rem;
    &::before,
    &::after {
      width: 0.65rem;
      cursor: pointer;
    }
  }
  input[type="text"],
  input[type="email"] {
